:root {
  font-size: 62.5%;
  --background-blur: rgb(0, 0, 0, 0.5);
  --background-gray: hsl(0, 0%, 96%);
  --shadow-clr: hsl(0, 0%, 80%);
  --shadow-clr-dark: hsl(0, 0%, 70%);

  --shadow: 0 0 1rem 0.3rem var(--shadow-clr);

  /* white ------------------*/
  --clr-wb-100: hsl(0, 0%, 100%);
  /* black ---------------*/
  --clr-wb-900: hsl(0, 0%, 0%);
  --clr-wb-800: hsl(0, 0%, 8%);
  --clr-wb-700: hsl(0, 0%, 20%);
  --clr-wb-500: hsl(0, 0%, 30%);
  --clr-wb-450: hsl(0, 0%, 37%);
  --clr-wb-400: hsl(0, 0%, 45%);
  --clr-wb-300: hsl(0, 0%, 65%);
  --clr-wb-200: hsl(0, 0%, 80%);

  /* Blue----------------- */
  --clr-blue-700: hsl(203, 100%, 40%);
  --clr-blue-500: hsl(203, 100%, 60%);
  /* green--------------------------- */
  --clr-green-700: hsl(103, 21%, 25%);
  --clr-green-500: hsl(103, 21%, 40%);
  --clr-green-400: hsl(143, 93%, 32%);
  /*  red--------------------------- */
  --clr-red-400: hsl(356, 100%, 53%);
}
@font-face {
  font-family: amiko-font;
  src: url("assets/fonts/Amiko-Regular.ttf") format("opentype");
}
@font-face {
  font-family: amiko-med;
  src: url("assets/fonts/Amiko-SemiBold.ttf") format("opentype");
}
@font-face {
  font-family: amiko-bold;
  src: url("assets/fonts/Amiko-Bold.ttf") format("opentype");
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
